@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 800;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-ExtraBold.woff2") format("woff2"), url("../fonts/Montserrat-ExtraBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #2D383F;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

:root {
  --swiper-theme-color: #ffffff;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.disabled {
  opacity: 0.1;
  pointer-events: none;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 1200px;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .container {
    width: 100%;
    padding: 0 5.86%;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 4.7%;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #0266C1;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:hover, .btn:focus-visible {
  outline: none;
  color: #1c3374;
  background-color: transparent;
  border: 1px solid #1c3374;
}
.btn:active {
  background-color: #1c3374;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  min-height: 62px;
  margin: 0 auto;
  color: #ffffff;
  z-index: 3;
}
.header .header__wrapper {
  padding-top: 40px;
}
.header .container {
  position: relative;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1199px) {
  .header {
    min-height: 54px;
  }
  .header .header__wrapper {
    padding-top: 30px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 44px;
  }
  .header .container {
    flex-wrap: wrap;
    column-gap: 25px;
  }
  .header .header__wrapper {
    padding-top: 15px;
  }
}

.header__tel {
  display: flex;
  align-items: center;
  min-width: 142px;
  height: 62px;
  font-size: 18px;
  font-weight: 600;
  line-height: 100%;
  margin-left: 30px;
}
.header__tel:hover, .header__tel:focus-visible {
  outline: none;
  color: #1c3374;
}
.header__tel:active {
  color: #0266C1;
}
@media (max-width: 1199px) {
  .header__tel {
    min-width: 126px;
    height: 44px;
    font-size: 16px;
    margin-left: 0;
  }
}
@media (max-width: 767px) {
  .header__tel {
    min-width: 110px;
    height: 14px;
    font-size: 14px;
    margin-left: auto;
  }
}

@media (max-width: 1199px) {
  .header.open-menu {
    height: 100%;
    background: var(--overlay-50, rgba(0, 0, 0, 0.5));
  }
  .header.open-menu .header__wrapper {
    background-color: #ffffff;
  }
  .header.open-menu ul {
    display: block;
  }
  .header.open-menu a {
    color: #2D383F;
  }
  .header.open-menu .logo {
    margin-left: 0;
    margin-right: 7px;
  }
  .header.open-menu .menu__toggle::after {
    position: absolute;
    content: "";
    top: 11px;
    left: 0;
    width: 22px;
    height: 22px;
    background-image: url("../img/svg/menu-close.svg");
  }
}
@media (max-width: 767px) {
  .header.open-menu .container {
    column-gap: 0;
  }
  .header.open-menu .logo {
    height: fit-content;
    margin-right: 0;
  }
  .header.open-menu .header__tel {
    position: absolute;
    top: 42px;
    right: 4.7%;
  }
  .header.open-menu .menu__toggle {
    height: 54px;
  }
  .header.open-menu .menu__toggle::after {
    top: 0;
  }
}

@media (max-width: 1199px) {
  .header.no-js ul {
    margin-top: 16px;
  }
  .header.no-js .menu__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .header.no-js ul {
    margin-top: 8px;
  }
}

.hero {
  position: relative;
}

.hero__swiper {
  position: relative;
}
.hero__swiper .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  opacity: 1;
  border: 1px solid #ffffff;
  background: none;
}
.hero__swiper .swiper-pagination-bullet-active {
  background: #ffffff;
}
@media (max-width: 767px) {
  .hero__swiper .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
  }
}

.hero__swiper-slide {
  position: relative;
  width: 100%;
  min-height: 800px;
  background-size: 100%;
  background-repeat: no-repeat;
}
.hero__swiper-slide img {
  position: absolute;
  top: 0;
  left: 0;
}
.hero__swiper-slide::before {
  position: absolute;
  content: "";
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(143deg, rgba(15, 20, 41, 0.33) 0%, rgba(15, 20, 41, 0) 100%);
  z-index: 1;
}
@media (max-width: 1199px) {
  .hero__swiper-slide {
    min-height: 1024px;
    padding: 0 5.86%;
  }
}
@media (max-width: 767px) {
  .hero__swiper-slide {
    min-height: 568px;
    padding: 0 4.69%;
  }
  .hero__swiper-slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.hero__wrapper {
  width: 100%;
  max-width: 1200px;
  height: 100%;
  margin: 182px auto 80px;
}
@media (max-width: 1199px) {
  .hero__wrapper {
    margin: 174px auto 90px;
  }
}
@media (max-width: 767px) {
  .hero__wrapper {
    margin: 139px auto 40px;
  }
}

.hero__content {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1200px;
  min-height: 538px;
  color: #ffffff;
  z-index: 1;
}
.hero__content h2 {
  width: 100%;
  margin: 0 0 25px;
  font-size: 77px;
  line-height: 100%;
  font-weight: 800;
  z-index: 1;
}
.hero__content p {
  max-width: 464px;
  margin: 0 0 36px;
  font-size: 17px;
  line-height: 141.2%;
}
.hero__content ul {
  display: flex;
  gap: 10px;
  margin: auto 0 0;
  padding: 0;
  list-style: none;
}
.hero__content li {
  position: relative;
  width: fit-content;
  max-width: 185px;
  min-height: 44px;
  padding: 13px 19px 13px 39px;
  border: 1px solid #ffffff;
  font-size: 12px;
  line-height: 133%;
}
.hero__content li::before {
  position: absolute;
  content: "";
  top: 13px;
  left: 20px;
  width: 12px;
  height: 12px;
}
.hero__content li:first-of-type::before {
  background-image: url("../img/svg/moon.svg");
}
.hero__content li:last-of-type::before {
  background-image: url("../img/svg/level.svg");
}
@media (max-width: 1199px) {
  .hero__content {
    align-items: center;
    max-width: 678px;
    text-align: center;
  }
  .hero__content h2 {
    font-size: 56px;
    margin-bottom: 15px;
  }
  .hero__content p {
    margin-bottom: 25px;
  }
  .hero__content ul {
    display: none;
  }
}
@media (max-width: 767px) {
  .hero__content {
    max-width: 290px;
  }
  .hero__content h2 {
    font-size: 30px;
  }
  .hero__content p {
    font-size: 15px;
    line-height: 133%;
    margin-bottom: 20px;
  }
}

.hero__button {
  width: 220px;
  min-height: 58px;
  padding: 21px 10px;
}
@media (max-width: 1199px) {
  .hero__button {
    width: 206px;
  }
}
@media (max-width: 767px) {
  .hero__button {
    width: 180px;
    min-height: 52px;
    padding: 18px 10px;
  }
}

.hero__wrapper-video {
  position: absolute;
  top: 119px;
  right: 0;
}
.hero__wrapper-video video {
  width: 482px;
  height: 317px;
}
@media (max-width: 1199px) {
  .hero__wrapper-video {
    position: relative;
    top: 50px;
  }
  .hero__wrapper-video video {
    width: 442px;
    height: 300px;
    object-fit: unset;
  }
}
@media (max-width: 767px) {
  .hero__wrapper-video {
    display: none;
  }
}

.hero__video-button {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 56px;
  height: 56px;
  border: 1px solid #ffffff;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.2);
  margin: auto;
}
.hero__video-button:hover, .hero__video-button:focus-visible {
  outline: none;
  border-color: #1c3374;
  background-color: transparent;
}
.hero__video-button:hover svg, .hero__video-button:focus-visible svg {
  stroke: #000000;
}
.hero__video-button:active {
  border-color: #1c3374;
  background-color: #1c3374;
}
.hero__video-button:disabled {
  border-color: #0266C1;
  background-color: transparent;
  opacity: 0.2;
}
.hero__video-button:disabled svg {
  fill: #0266C1;
}
.hero__video-button svg {
  position: absolute;
  fill: #ffffff;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}

.hero__wrapper-photos {
  position: absolute;
  top: 20px;
  right: 0;
  width: 410px;
  min-height: 459px;
}
@media (max-width: 1199px) {
  .hero__wrapper-photos {
    position: relative;
    top: 34px;
    width: 442px;
    min-height: 365px;
  }
  .hero__wrapper-photos img {
    width: 266px;
    height: 351px;
  }
}
@media (max-width: 767px) {
  .hero__wrapper-photos {
    display: none;
  }
}

.hero__photo-two img:last-child {
  right: 0;
  bottom: 0;
  top: auto;
  left: auto;
}
@media (max-width: 1199px) {
  .hero__photo-two img {
    width: 236px;
    height: 312px;
  }
}

.hero__wrapper-audio-player {
  position: absolute;
  right: 0;
  top: 119px;
}
@media (max-width: 1199px) {
  .hero__wrapper-audio-player {
    position: relative;
    top: 49px;
  }
}
@media (max-width: 767px) {
  .hero__wrapper-audio-player {
    display: none;
  }
}

.hero-swiper__pagination {
  position: absolute;
  display: flex;
  justify-content: end;
  gap: 5px;
  transition: 0.3s opacity;
  transform: translate3d(0, 0, 0);
  right: 0;
  margin: 0 auto 88px;
  max-width: 1200px;
  z-index: 3;
}
@media (max-width: 1199px) {
  .hero-swiper__pagination {
    justify-content: center;
    margin-bottom: 82px;
  }
}
@media (max-width: 767px) {
  .hero-swiper__pagination {
    justify-content: center;
    margin-bottom: 32px;
  }
}

.menu {
  margin-top: 20px;
}
.menu ul {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu li {
  margin: 0;
  padding: 0;
}
.menu li:not(:last-child) {
  margin-right: 22px;
}
.menu a {
  display: flex;
  width: fit-content;
  padding: 2px 0;
  padding: 3px 5px;
}
.menu a:hover, .menu a:focus-visible {
  border-bottom: 1px solid currentColor;
}
.menu a:active {
  padding: 2px 4px;
  border: 1px solid currentColor;
}
@media (max-width: 1199px) {
  .menu {
    margin-top: 0;
    display: grid;
    row-gap: 47px;
    order: -1;
  }
  .menu ul {
    display: none;
    order: 1;
    padding-bottom: 67px;
  }
  .menu li:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .menu a {
    height: 22px;
    font-weight: 700;
    letter-spacing: 0.64px;
    text-transform: uppercase;
    padding-left: 0;
  }
  .menu a:hover, .menu a:focus-visible {
    padding: 3px 5px;
    border-bottom: 1px solid currentColor;
  }
  .menu a:active {
    padding: 2px 4px;
    border: 1px solid currentColor;
  }
}
@media (max-width: 767px) {
  .menu {
    row-gap: 28px;
  }
  .menu ul {
    padding-bottom: 46px;
  }
  .menu li:not(:last-child) {
    margin-bottom: 16px;
  }
  .menu a {
    height: 20px;
    font-size: 14px;
    letter-spacing: 0.56px;
  }
}

.menu__toggle {
  position: relative;
  display: none;
}
@media (max-width: 1199px) {
  .menu__toggle {
    display: block;
    width: 26px;
    height: 44px;
    border: none;
    background-color: transparent;
  }
  .menu__toggle::after {
    position: absolute;
    content: "";
    top: 9px;
    left: 0;
    width: 26px;
    height: 26px;
    background-image: url("../img/svg/menu-open.svg");
  }
}
@media (max-width: 767px) {
  .menu__toggle {
    height: 42px;
  }
  .menu__toggle::after {
    top: 0;
  }
}

.logo {
  min-width: 205px;
  margin-right: 30px;
}
.logo:hover, .logo:focus-visible {
  outline: none;
  color: #1c3374;
}
.logo:active {
  color: #0266C1;
}
.logo svg {
  fill: currentColor;
  width: 205px;
  height: 62px;
}
@media (max-width: 1199px) {
  .logo {
    min-width: 144px;
    margin-right: 0;
    margin-left: 100px;
  }
  .logo svg {
    width: 144px;
    height: 44px;
  }
}
@media (max-width: 767px) {
  .logo {
    min-width: 112px;
  }
  .logo svg {
    width: 112px;
    height: 34px;
  }
}

